import { Observable, ObservedValueOf, OperatorFunction, Subscribable } from 'rxjs';
import { catchError } from 'rxjs/operators';

/**
 * catchError with error logging operator
 *
 * @param fn - callback function
 */
export function catchErrorWithLog<T, O extends Subscribable<any>>(
  fn: (error: any) => O,
): OperatorFunction<T, T | ObservedValueOf<O>> {
  return (source: Observable<T>): Observable<T | ObservedValueOf<O>> =>
    source.pipe(
      catchError((state) => {
        console.error(state);

        return fn(state);
      }),
    );
}
